import {Route, UrlMatchResult} from './config';
import {UrlSegment, UrlSegmentGroup} from './url_tree';
/**
 * \@whatItDoes Name of the primary outlet.
 * 
 * \@stable
 */
export const PRIMARY_OUTLET = 'primary';

/**
 * A collection of parameters.
 *
 * @stable
 */
export type Params = {
  [key: string]: any
};

const /** @type {?} */ NAVIGATION_CANCELING_ERROR = 'ngNavigationCancelingError';
/**
 * @param {?} message
 * @return {?}
 */
export function navigationCancelingError(message: string) {
  const /** @type {?} */ error = Error('NavigationCancelingError: ' + message);
  ( /** @type {?} */((error as any)))[NAVIGATION_CANCELING_ERROR] = true;
  return error;
}
/**
 * @param {?} error
 * @return {?}
 */
export function isNavigationCancelingError(error: Error) {
  return ( /** @type {?} */((error as any)))[NAVIGATION_CANCELING_ERROR];
}
/**
 * @param {?} segments
 * @param {?} segmentGroup
 * @param {?} route
 * @return {?}
 */
export function defaultUrlMatcher(
    segments: UrlSegment[], segmentGroup: UrlSegmentGroup, route: Route): UrlMatchResult {
  const /** @type {?} */ path = route.path;
  const /** @type {?} */ parts = path.split('/');
  const /** @type {?} */ posParams: {[key: string]: UrlSegment} = {};
  const /** @type {?} */ consumed: UrlSegment[] = [];

  let /** @type {?} */ currentIndex = 0;

  for (let /** @type {?} */ i = 0; i < parts.length; ++i) {
    if (currentIndex >= segments.length) return null;
    const /** @type {?} */ current = segments[currentIndex];

    const /** @type {?} */ p = parts[i];
    const /** @type {?} */ isPosParam = p.startsWith(':');

    if (!isPosParam && p !== current.path) return null;
    if (isPosParam) {
      posParams[p.substring(1)] = current;
    }
    consumed.push(current);
    currentIndex++;
  }

  if (route.pathMatch === 'full' &&
      (segmentGroup.hasChildren() || currentIndex < segments.length)) {
    return null;
  } else {
    return {consumed, posParams};
  }
}
